import React from 'react';
import {FaThumbsUp, FaMap} from 'react-icons/fa'  
import {db,rdb} from '../firebase'
import './list.css'
import ListItem from './listItem';
var length;

  class ClassList extends React.Component{
    state = {
        classes:null,
        name:"null",
      }
      
      constructor(props){
        super(props);
        this.state = {
          words: []
        }
      }

      componentDidMount(){
        var userId = this.props.userId
        const data = rdb.ref('Users').child(userId);    
        data.on('value',(snapshot)=>{
          var words = snapshot.val();
          length = snapshot.numChildren()
          var newState = [];
          for(let word in words){
            newState.push({
              id: words[word].id,
            })
          }
          this.setState({words:newState})
        });
      }
     
      render(){
          return(
              <div>
                <div style={{backgroundColor:'#E6E6E6'}}>
                    <div class='number'><FaThumbsUp size='12' style={{marginRight:'5px'}}/> Found {length} Pidgin classes around you.</div>
                     
                        {
                          this.state.words.map((word)=>{
                            return <ListItem classID={word.id} uid={this.props.userId} />
                          })
                        }
                       
                  </div>
            </div>
          )
      }
  }
  export default ClassList;