import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import { FaSchool, FaMusic, FaHandsHelping } from 'react-icons/fa';
import {Link} from 'react-router-dom'

const useStyles = makeStyles({
  root: {
    width: '100%',
    position: "fixed",
    bottom: 0,
    zIndex: '100'
  },
});

export default function SimpleBottomNavigation() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  return (
    <BottomNavigation
      showLabels
      className={classes.root}
      value={value}
      onChange={(event, newValue) => {
        setValue(newValue);
      }}
    >
      <BottomNavigationAction label="Schools" icon={<FaSchool />} component={Link} to={'./school'} />
      <BottomNavigationAction label="Classes" icon={<FaMusic />} component={Link} to={'./search'} />
      <BottomNavigationAction label="Help" icon={<FaHandsHelping />} />
    </BottomNavigation>
  );
}
